import React from 'react';
import one from './1.jpg';
import two from './2.jpg';
import three from './3.jpg';
import four from './4.jpg';
import five from './5.jpg';
import ViewPicture from './ViewPicture.js';
import M from "materialize-css";

const carouselOptions = {
	duration: 300,
	dist: 50,
	numVisible: 5,
};

const modalOptions = {
	opacity: 0
};

class PhotoCarousel extends React.Component {
  constructor(props) {
	super(props);
	this.state = {
		picture: one,
	    showModal: false,
		modal: null
	};

  }

  componentDidMount() {
	let elem = document.querySelector('.carousel');
   	M.Carousel.init(elem, carouselOptions);
    let elem2 = document.querySelector('.modal');
	let modal = M.Modal.init(elem2, modalOptions);
	this.setState({modal: modal});
  }


  handleClick = t => {
	const pic = t.target.attributes.alt.value;
	
	if (pic === "one") {
		this.setState({picture: one});
	} else if (pic === "two") {
		this.setState({picture: two});
	} else if (pic === "three") {
		this.setState({picture: three});
	} else if (pic === "four") {
		this.setState({picture: four});
	} else if (pic === "five") {
		this.setState({picture: five});
	}

	this.state.modal.open();		
  }

  render() {
	return (
	  <div style={{"height": "100%"}}>
		<ViewPicture current={this.state.picture}/>
	  	<h2 style={{"margin-bottom": "-80px"}}>Album 1</h2>
	  	<div class="carousel" style={{"height": "500px"}}>
		  <a class="carousel-item" href="#one!"><img alt="one" src={one} onClick={this.handleClick} /></a>
		  <a class="carousel-item" href="#two!"><img alt="two" src={two} onClick={this.handleClick} /></a>
		  <a class="carousel-item" href="#three!"><img alt="three" src={three} onClick={this.handleClick}  /></a>
		  <a class="carousel-item" href="#four!"><img alt="four" src={four} onClick={this.handleClick}/></a>
		  <a class="carousel-item" href="#five!"><img alt="five" src={five} onClick={this.handleClick}/></a>
	    </div>
	  </div>
	);
  }
}

export default PhotoCarousel;

