import React from 'react';

class ViewPicture extends React.Component {
  constructor(props) {
	super(props);
	this.myRef = React.createRef();
  }

  componentDidUpdate() {
	  this.myRef.current.scrollTo(0, 0);
  }

  render() {
	return (
	  <div
        ref={this.myRef}
        id="modal1"
        className="modal"
		style={{width: "75%", "max-height": "100%", "background-color": "rgba(255, 255, 255, 0.75)", position: "absolute"}}

      >
        <div className="modal-content" style={{}}>
          <a href="#" class="modal-close waves-effect waves-green btn">
			Exit
          </a>
          <h4>Caption</h4>
          <p>*Date*</p>
		    <img style={{"width": "100%", "height": "100%"}} src={this.props.current} />
        </div>
      </div>
	);
  }
}

export default ViewPicture;

