import React from 'react';
import M from "materialize-css";

class MenuBar extends React.Component {
  componentDidMount() {
	let elem = document.querySelector('.tabs');
	M.Tabs.init(elem);
  }

  render() {
	return (
	  <div class="row">
        <div class="col s12">
          <ul class="tabs">
            <li class="tab col s6"><a class="active" href="#cooking">Photos</a></li>
        	<li class="tab col s6"><a href="#cooking">Cooking</a></li>
          </ul>
    	</div>
  	  </div>
	);
  }
}

export default MenuBar;
